@keyframes menuFadeIn {
    0% {
        opacity: 0;
    }

    50% {
        transform: translateY(-40%);
        opacity: 0;
    }

    90% {
        opacity: 1;
    }

    100% {
        opacity: 1;
        transform: translateY(0%);
    }
}

@keyframes menuFadeOut {
    0% {
        opacity: 1;
        transform: translateY(0%);
    }

    80% {
        opacity: 0;
    }

    100% {
        transform: translateY(-40%);
        opacity: 0;
    }
}
